.btn {
  display: inline-block;
  outline: none;
  border: none;
  position: relative;
  overflow: hidden;
  font-size: 1rem;
  padding: 0.375em 1.25em;
  color: rgb(255, 255, 255);
  border-radius: 4px;
  transition: all 0.35s ease-out;
  text-transform: capitalize;
  z-index: 1;
}
.btn::before, .btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  z-index: -1;
}
.btn::before {
  left: -30%;
}
.btn::after {
  left: 125%;
}
.btn:hover {
  cursor: pointer;
}
.btn:hover::before {
  animation: collisionLeft 0.35s both;
  animation-direction: alternate;
}
.btn:hover::after {
  animation: collisionRight 0.35s both;
  animation-direction: alternate;
}
@media only screen and (min-width: 48em) {
  .btn {
    font-size: 1.125rem;
  }
}
@media only screen and (min-width: 62em) {
  .btn {
    font-size: 1.25rem;
  }
}
@keyframes collisionLeft {
  0% {
    left: -30%;
    width: 20px;
    height: 20px;
  }
  50% {
    left: 50%;
    width: 40px;
    height: 40px;
  }
  100% {
    left: 50%;
    width: 300px;
    height: 300px;
  }
}
@keyframes collisionRight {
  0% {
    left: 125%;
    width: 20px;
    height: 20px;
  }
  50% {
    left: 50%;
    width: 40px;
    height: 40px;
  }
  100% {
    left: 50%;
    width: 300px;
    height: 300px;
  }
}

